<!--
 * @descripttion: 预览课表-教师课表
 * @version: v1.0
 * @Author: WuQiao
 * @Date: 2021-5-28 13:26:12
-->
<template>
  <div class="teacher-timetable">
    <div class="left">
      <a-tree
        :tree-data="teaListst"
        :replace-fields="replaceFields"
        :blockNode="true"
        :expandedKeys="openKeys"
        :selectedKeys="selectedKeys"
        @expand="onExpand"
        @select="onSelect"
      />
      <!-- <a-menu :selectedKeys="selectedKeys" @select="select" mode="inline">
        <a-menu-item v-for="i in teaListst" :key="i.teacherId">{{
          i.teacherName
        }}</a-menu-item>
      </a-menu> -->
    </div>
    <div class="right">
      <div class="select">
        筛选：<a-checkbox-group
          :disabled="tableLoading"
          :value="checkedList"
          :options="plainOptions"
          @change="onChange"
        />
      </div>
      <div class="rightcontent">
        <a-table
          class="timetable"
          :scroll="{ y: 'calc(100vh - 280px)' }"
          :columns="columns"
          :data-source="timetableList"
          :loading="tableLoading"
          :pagination="false"
          :rowKey="(row) => row.id"
          bordered
        >
          <!-- <div class="vacation" v-if="timetableList.length && timetableList[0].lesMon.isHoliday">休假</div> -->
          <!-- 周一到周日课程 -->
          <template slot="lesSort" slot-scope="text">
            <b>
              {{ text }}
            </b>
          </template>
          <template
            v-for="(item, index) in lesWeek"
            :slot="item"
            slot-scope="text"
          >
            <div :key="index">
              <div
                v-show="!i.isHoliday && i.lesId"
                class="unit"
                :key="d"
                v-for="(i, d) in text"
              >
                <b class="little-unit" v-if="filtrateStatus.course"
                  >{{ i.lesSub
                  }}{{
                    i.dbWeek == 1 ? "[单周]" : i.dbWeek == 2 ? "[双周]" : ""
                  }}</b
                >
                <p>
                  <span
                    v-for="(o, q) in i.lesTeacher"
                    :key="q"
                    v-show="filtrateStatus.teacher && o.main"
                    >{{ o.name }}
                    <a-popover
                      trigger="hover"
                      :content="getLesTeacher(i.lesTeacher)"
                      v-show="i.lesTeacher.length > 1"
                    >
                      <a-icon
                        type="user"
                        style="font-size: 15px; cursor: pointer; color: #1890ff"
                      />
                    </a-popover>
                  </span>
                  {{
                    filtrateStatus.classRoom && filtrateStatus.teacher
                      ? "/"
                      : ""
                  }}
                  <span>{{ filtrateStatus.classRoom ? i.lesPlace : "" }}</span>
                </p>
                <span class="little-unit"
                  >{{ filtrateStatus.class ? i.classes + "/" : ""
                  }}{{ i.lesStu }}人</span
                >
              </div>
            </div>
          </template>
        </a-table>
      </div>
    </div>
  </div>
</template>
 
<script>
import { list } from "./data";
const columns = [
  {
    title: "时段",
    dataIndex: "diffNoon",
    key: "diffNoon",
    align: "center",
    width: "6%",
    scopedSlots: { customRender: "diffNoon" },
    customCell() {
      return {
        style: {
          background: "#fafafa",
        },
      };
    },
    customRender(_, row) {
      return {
        children: row.diffNoon,
        attrs: {
          rowSpan: row.diffNoonRowSpan,
        },
      };
    },
  },
  {
    title: "节次",
    dataIndex: "lesSort",
    key: "lesSort",
    align: "center",
    width: "10%",
    scopedSlots: { customRender: "lesSort" },
    customCell() {
      return {
        style: {
          background: "#fafafa",
        },
      };
    },
  },
  {
    title: "星期一",
    dataIndex: "lesMon",
    key: "lesMon",
    align: "center",
    width: "12%",
    scopedSlots: { customRender: "lesMon" },
    slots: { title: "lesMon" },
  },
  {
    title: "星期二",
    dataIndex: "lesTue",
    key: "lesTue",
    align: "center",
    width: "12%",
    scopedSlots: { customRender: "lesTue" },
    slots: { title: "lesTue" },
  },
  {
    title: "星期三",
    dataIndex: "lesWed",
    key: "lesWed",
    align: "center",
    width: "12%",
    scopedSlots: { customRender: "lesWed" },
    slots: { title: "lesWed" },
  },
  {
    title: "星期四",
    dataIndex: "lesThu",
    key: "lesThu",
    align: "center",
    width: "12%",
    scopedSlots: { customRender: "lesThu" },
    slots: { title: "lesThu" },
  },
  {
    title: "星期五",
    dataIndex: "lesFri",
    key: "lesFri",
    align: "center",
    width: "12%",
    scopedSlots: { customRender: "lesFri" },
    slots: { title: "lesFri" },
  },
  {
    title: "星期六",
    dataIndex: "lesSat",
    key: "lesSat",
    align: "center",
    width: "12%",
    scopedSlots: { customRender: "lesSat" },
    slots: { title: "lesSat" },
  },
  {
    title: "星期日",
    dataIndex: "lesSun",
    key: "lesSun",
    align: "center",
    width: "12%",
    scopedSlots: { customRender: "lesSun" },
    slots: { title: "lesSun" },
  },
];
const timetableList = list;
export default {
  name: "TeacherTimetable",
  components: {},
  props: {},
  data() {
    return {
      columns,
      timetableList: [],
      plainOptions: ["教师", "教室", "班级", "课程"],
      checkedList: ["教师", "教室", "班级", "课程"],
      tableLoading: false,
      teaListst: [],
      replaceFields: {
        children: "list",
        title: "teacherName",
        key: "teacherId",
      },
      selectedKeys: [],
      openKeys: [],
      lesWeek: [
        "lesMon",
        "lesTue",
        "lesWed",
        "lesThu",
        "lesFri",
        "lesSat",
        "lesSun",
      ],
    };
  },
  computed: {
    /**
     * @desc 筛选状态
     */
    filtrateStatus() {
      const { checkedList } = this;
      let obj = {
        teacher: checkedList.includes("教师"),
        classRoom: checkedList.includes("教室"),
        class: checkedList.includes("班级"),
        course: checkedList.includes("课程"),
      };
      return obj;
    },
  },
  mounted() {
    this.arrLessonId = sessionStorage.getItem("arrLessonId");
    this.getTeacherList();
  },
  methods: {
    onChange(checkedList) {
      if (!checkedList.length) return this.$message.warn("至少勾选一个");
      this.checkedList = checkedList;
    },
    getLesTeacher(lesTeacher) {
      let con = "副教师：";
      let _lesTeacher = lesTeacher.filter((item) => !item.main);
      _lesTeacher.map((item, index) => {
        if (!item.main) {
          if (index == 0) {
            con += item.name;
          } else {
            con = con + "/" + item.name;
          }
        }
      });
      return con;
    },
    toggleClass(teacherId) {
      this.getTeacherTimetable(teacherId);
    },
    async getTeacherList() {
      const arrLessonId = this.arrLessonId;
      const data = await this.$api.ArrLessonRule.getTeacherList({
        planId: arrLessonId,
      });
      if (data.code == 200) {
        let dataList = data.data || [],
          selectedKeys = "";
        dataList.map((item, index) => {
          item.teacherName = item.group;
          item.teacherId = item.groupId;
          if (item.list && item.list.length && !selectedKeys) {
            selectedKeys = item.list[0].teacherId;
            this.openKeys = [item.groupId];
          }
        });

        this.teaListst = dataList;
        this.selectedKeys = [selectedKeys];

        if (selectedKeys) {
          this.getTeacherTimetable(selectedKeys);
        }
      } else {
        this.$message.error("请求失败！" + res.message);
      }
    },
    onExpand(expandedKeys, info) {
      this.openKeys = expandedKeys;
    },
    onSelect(selectedKeys, info) {
      if (selectedKeys && selectedKeys.length) {
        let key = selectedKeys[0];
        let groupId = info.selectedNodes[0].data.props.groupId;
        //选中分组节点 则相应展开或收起节点
        if (groupId) {
          const openKeyIndex = this.openKeys.findIndex((item) => {
            return item === groupId;
          });
          if (openKeyIndex !== -1) {
            this.openKeys.splice(openKeyIndex, 1);
          } else {
            this.openKeys.push(groupId);
          }
        } else {
          //选中教师节点
          this.selectedKeys = selectedKeys;
          this.getTeacherTimetable(key);
        }
      }
    },

    async getTeacherTimetable(teacherId) {
      const arrLessonId = this.arrLessonId;
      const params = {
        arrLessonId,
        teacherId,
      };
      this.tableLoading = true;
      const data = await this.$api.PreviewTimetable.getTeacherTimetable(params);
      if (data.code == 200) {
        this.timetableList = data.data.list;
        this.rowSpanMerge("diffNoon");
      } else {
        this.$message.error("请求失败！" + data.message);
      }
      this.tableLoading = false;
    },
    // 表格第一列合并
    rowSpanMerge(key) {
      const arr = this.timetableList
        .reduce((result, item) => {
          if (result.indexOf(item[key]) < 0) {
            result.push(item[key]);
          }
          return result;
        }, [])
        .reduce((result, keys) => {
          const children = this.timetableList.filter(
            (item) => item[key] === keys
          );
          result = result.concat(
            children.map((item, index) => ({
              ...item,
              [`${key}RowSpan`]: index === 0 ? children.length : 0,
            }))
          );
          return result;
        }, []);
      this.timetableList = arr;
    },
    // openChange(openKeys) {
    //   this.openKeys = openKeys;
    // },
    // select({ item, key, selectedKeys }) {
    //   this.selectedKeys = selectedKeys;
    //   this.getTeacherTimetable(selectedKeys[0]);
    // },
  },
};
</script>
 
<style scoped lang="less">
// /deep/ table tbody tr:hover > td {
//   background-color: #ffffff !important;
// }
// /deep/tbody tr td {
//   height: 120px;
//   width: 120px;
//   &:hover {
//     background: #13c2c2 !important;
//     color: white;
//   }
// }
.teacher-timetable {
  width: 100%;
  background: white;
  min-height: 740px;
  height: calc(100vh - 220px);
  display: flex;
  .left {
    width: 280px;
    margin-right: 20px;
    overflow: auto;
  }
  .right {
    flex: 1;
    .rightcontent {
      height: calc(100% - 32px);
      overflow: auto;
    }
    .timetable {
      min-width: 1299px;
      /deep/.unit {
        display: block;
        position: relative;
        transition: all 0.3s;
        background-image: linear-gradient(to right, rgba(#409fff, 0.15), #fff);
        padding: 5px 0 5px 5px;
        .little-unit {
          display: block;
          text-align: left;
        }
        p {
          display: flex;
          margin-bottom: 0;
          span {
            display: inline-block;
          }
        }
        // &:hover {
        //   background-color: #E6F7FF;
        // }
      }
      .unit + .unit {
        margin-top: 10px;
      }
    }
  }
  .select {
    height: 32px;
    padding-left: 5px;
  }
  /deep/ .ant-table-thead > tr > th {
    background-color: #409fff;
    border-color: #79bcff;
    color: #fff;
    text-align: center;
  }
}
</style>